"use client";

import { useState, useEffect } from "react";
import "./send-email.css";
import AuthGuard from "@/components/AuthGuard";
import AdvanceEmailBuilder from "./advanceEmailEditor";

interface SendEmailProps {
  emailId?: string;
}

export default function SendEmail({ emailId }: SendEmailProps) {
  //  Multiple Emails State
  const [to, setTo] = useState<string[]>([]);
  const [inputValue, setInputValue] = useState("");

  const [subject, setSubject] = useState("");
  const [body, setBody] = useState("");
  const [senderName, setSenderName] = useState("");

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState(false);

  useEffect(() => {
    if (!emailId) return;

    const loadDraft = async () => {
      try {
        const res = await fetch(`/api/emails/${emailId}`, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });
        const data = await res.json();

        if (!res.ok) throw new Error(data.error);

        setTo([data.to || ""]);
        setSubject(data.subject || "");
        setBody(data.body || "");
        setSenderName(data.senderName || "");
        setDraftId(data._id);
      } catch (err) {
        console.error("Draft load error", err);
      }
    };

    loadDraft();
  }, [emailId]);

  //  Email validation
  const isValidEmail = (email: string) =>
    /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);

  //  Add email chip
  const addEmail = (email: string) => {
    email = email.trim();

    if (!isValidEmail(email)) {
      setError("Invalid email format");
      return;
    }

    if (!to.includes(email)) {
      setTo([email, ...to]); // Add on top
    }

    setInputValue("");
    setError(null);
  };

  //  Enter key support
  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Enter" && inputValue.trim()) {
      e.preventDefault();
      addEmail(inputValue);
    }
  };

  //  Remove email chip
  const removeEmail = (email: string) => {
    setTo(to.filter((e) => e !== email));
  };

  //  Paste multiple emails support
  const handlePaste = (e: React.ClipboardEvent<HTMLInputElement>) => {
    const paste = e.clipboardData.getData("text");
    const emails = paste.split(/[,;\s]+/);

    emails.forEach((email) => {
      if (isValidEmail(email) && !to.includes(email)) {
        setTo((prev) => [email, ...prev]);
      }
    });

    e.preventDefault();
  };

  // Load logged user
  useEffect(() => {
    const user = JSON.parse(localStorage.getItem("user") || "{}");
    setSenderName(user?.email || "");
  }, []);

  //  SEND EMAIL
  const sendEmail = async () => {
    setError(null);
    setSuccess(false);

    if (to.length === 0 || !subject || !body) {
      setError("All fields are required");
      return;
    }

    setLoading(true);

    try {
      const res = await fetch("/api/send", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
        body: JSON.stringify({
          to,
          subject,
          body,
          senderName,
        }),
      });

      const data = await res.json();

      if (!res.ok) throw new Error(data.error);

      setSuccess(true);
      setTo([]);
      setSubject("");
      setBody("");
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
    } catch (err: any) {
      setError(err.message || "Failed to send email");
    } finally {
      setLoading(false);
    }
  };

  const [draftId, setDraftId] = useState<string | null>(emailId || null);

  const saveAsDraft = async () => {
    setLoading(true);

    try {
      const res = await fetch("/api/draft", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
        body: JSON.stringify({
          id: draftId,
          to,
          subject,
          body,
          senderName,
        }),
      });

      const data = await res.json();

      if (!res.ok) throw new Error(data.error);

      if (!draftId) setDraftId(data._id);

      setSuccess(true);
      setTo([]);
      setSubject("");
      setBody("");
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
    } catch (err: any) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  const isFormValid = () => {
    return to.length > 0 && subject.trim() !== "" && body.trim() !== "";
  };

  return (
    <AuthGuard>
      <div className="container">
        <div className="card">
          <h1>Send Email</h1>
          <p className="subtitle">Track opens in real-time</p>

          {error && <div className="error">{error}</div>}
          {success && <div className="success">Email sent successfully!</div>}

          {/* MULTI EMAIL INPUT */}
          <label>To</label>

          <div className="email-wrapper">
            {to.map((email) => (
              <span key={email} className="chip">
                {email}
                <button onClick={() => removeEmail(email)}>Ã—</button>
              </span>
            ))}

            <input
              type="email"
              placeholder="Type email & press Enter"
              value={inputValue}
              onChange={(e) => setInputValue(e.target.value)}
              onKeyDown={handleKeyDown}
              onPaste={handlePaste}
            />
          </div>

          {/* SUBJECT */}
          <label>Subject</label>
          <input
            value={subject}
            placeholder="Enter subject"
            onChange={(e) => setSubject(e.target.value)}
          />

          {/* EMAIL BUILDER */}
          <label>Message</label>
          {/* <EmailBuilder body={body} setBody={setBody} /> */}
          <AdvanceEmailBuilder body={body} setBody={setBody} />

          <button
            className="send-button ml-2"
            disabled={loading || !to.length}
            onClick={saveAsDraft}
          >
            {loading ? "Save..." : "Save Email as Draft"}
          </button>

          {/* SEND BUTTON */}
          <button
            className="send-button"
            disabled={loading || !isFormValid()}
            onClick={sendEmail}
          >
            {loading ? "Sending..." : "Send Email"}
          </button>
        </div>
      </div>
    </AuthGuard>
  );
}
